.navbar {
    position: fixed;
    top: 0;
    margin-left: 20%;
    margin-right: 20%;
    width: 60%;
    overflow: hidden;
    background-color: white;
    z-index: 1;
    
    
}

.DarkMode .navbar {
    background-color: black;
}


.navbar > ul{
    list-style:none;
    display:flex;
    margin-left:auto;
    margin-right:auto;
    padding: 10px; 
    margin: 0;
   
   

}

.navbar > ul > li{
    margin-left:auto;
    margin-right:auto;
    margin-top: auto;
   
    
}
.navbar > ul > li > a{
    text-decoration:none;
    font-weight: bold;
}

.navbar > ul > li > a:hover{
    background-color:white;
    color: crimson;
}

.DarkMode .navbar > ul > li > a:hover{
    background-color:black;
   
}

a {
    color: black;
